import { BreadcrumbService } from '../../breadcrumb.service';

import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {CarService} from '../../demo/service/carservice';
import {EventService} from '../../demo/service/eventservice';
import {Car} from '../../demo/domain/car';
import {SelectItem} from 'primeng/primeng';

import {NodeService} from '../../demo/service/nodeservice';

import {TreeNode} from 'primeng/primeng';
import { Router } from '@angular/router';


import { AngularFireDatabase, AngularFireList } from 'angularfire2/database';

import { NotesComponent } from '../notes/notes';


@Component({
    
    templateUrl: './users.html',
    styleUrls:['./users.scss'],
    encapsulation: ViewEncapsulation.None,

    
})
export class UsersComponent implements OnInit {

    



    constructor(private carService: CarService, 
        private eventService: EventService,
         private nodeService: NodeService,
        private breadcrumbService: BreadcrumbService,
        public db:AngularFireDatabase,
         public router:Router) {

            
            this.itemList=db.list('users');

            this.breadcrumbService.setItems([
                { label: 'المستخدمين' },
            ]);
     }


     
  itemList:AngularFireList<any>;
  itemArray=[];
  selectedItemArray=[];

  userdata={
    code:'',
    userName:'',
    useremail:'',
    userphone:'',
  }

  cols: any[];
  users: any[];


  send(id){
  //    alert(id);

    this.router.navigate(['/notes',id]);


    

  }


    ngOnInit() {
   
  this.cols = [
      
            { field: 'userName', header: 'الاسم' },
            { field: 'useremail', header: 'الإيميل' },
            { field: 'userphone', header: 'الموبايل' },
            
        ];
    
        this.itemList.snapshotChanges().subscribe(actions=>{
            actions.forEach(action=>{
              let y= action.payload.toJSON()
              y['$key']=action.key
              this.itemArray.push(y as ListUserClass)
         //     this.datalen=actions.length;
         //     this.pagescount=(Math.floor(this.datalen / 5 ) + 1);
       })
          })
          console.log(this.itemArray);
      
         
         



    }

   

            
}
export class ListUserClass{
    $key:string;
    name:string;
    email:string;
    phone:string;
  }
  
  
  