import { element, $ } from 'protractor';
import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {CarService} from '../../demo/service/carservice';
import {EventService} from '../../demo/service/eventservice';
import {Car} from '../../demo/domain/car';
import { SelectItem, Message } from 'primeng/primeng';
import {BreadcrumbService} from '../../breadcrumb.service';

import {NodeService} from '../../demo/service/nodeservice';

import {TreeNode} from 'primeng/primeng';
import { Router, ActivatedRoute } from '@angular/router';

import {CardModule} from 'primeng/card';

import { AngularFireDatabase, AngularFireList } from 'angularfire2/database';
import { myservicesService } from '../../demo/service/myservices';


import {ConfirmDialogModule} from 'primeng/confirmdialog';
import {ConfirmationService} from 'primeng/api';
import { MessageService } from 'primeng/components/common/messageservice';

import {MessagesModule} from 'primeng/messages';
import {MessageModule} from 'primeng/message';


import {DialogModule} from 'primeng/dialog';
import { DomSanitizer } from '@angular/platform-browser';


@Component({
    
    templateUrl: './notes.html',
    styleUrls:['./notes.scss'],
    encapsulation: ViewEncapsulation.None,
    providers: [ConfirmationService]

    
})
export class NotesComponent implements OnInit {

    idemail:string;

    myurl:any="";

    msgs: Message[] = [];


    title:string='';
    det:string='';


  

    
    fval:number=0;
    endval:number=4;
    pagenum:number=1;
    countnum:number=0;

    lastPage:number=0;
    

    
    clear(){
        this.fval=0;
        this.endval=4;
        this.pagenum=1;
    }

    first(){

        if(this.pagenum !=1){
            this.fval=0;
            this.endval=4;
            this.pagenum=1;
           
        }
   
  
    }
    next(){
        if(this.pagenum<this.lastPage){
        this.fval+=4;
        this.endval+=4;
        this.pagenum+=1;
        }
    }
    prev(){
        if(this.pagenum!=1){
        this.fval-=4;
        this.endval-=4;
        this.pagenum-=1;
        }
    }
    last(){

        if(this.pagenum<this.lastPage){
        this.fval=this.pagenum * 4;
        this.endval=(this.pagenum * 4 )+ 4;
        this.pagenum=this.lastPage;
        }
      
    }
    constructor(private carService: CarService, 
        private eventService: EventService,
         private nodeService: NodeService,
        private breadcrumbService: BreadcrumbService,
        public db:AngularFireDatabase,
         public router:Router,
        private myserv:myservicesService,
    private confirmationService:ConfirmationService,
    private sanitizer: DomSanitizer,
    private route: ActivatedRoute,
    
  ) {



    this.myurl = this.sanitizer.bypassSecurityTrustResourceUrl('http://dhsahospital.com/fcm/');
    
    
            
            this.breadcrumbService.setItems([
                { label: ' التنبيهات ' },
            ]);
     }


     confirm(val,name) {
        this.confirmationService.confirm({
            message: 'هل تود حذف التنبيه: « ' + name + "  » حقا ?  ",
            header: 'حذف التنبيه رقم ' + val,
            acceptLabel:'نعم',
            rejectLabel:'لا',
            accept: () => {

                this.myurl = this.sanitizer.bypassSecurityTrustResourceUrl
                ("http://dhsahospital.com/fcm/del.php?id="+
                val);

                this.msgs.push({severity:'error', detail:'تم الحذف بنجاح'});
              setTimeout(() => {
                  this.msgs=[];
                  this.loadData();
              }, 700);

           
    
    
    
            },
        });
    }
    addnew()
{

    if(this.title==''){
        this.msgs.push({severity:'error', detail:'أدخل عنوان التنبيه'});
        setTimeout(() => {
            this.msgs=[];
        }, 700);
        return;
    }
    if(this.det==''){
        this.msgs.push({severity:'error', detail:'أدخل تفاصيل التنبيه'});
        setTimeout(() => {
            this.msgs=[];
        }, 700);
        return;
    }
    

    


    this.myurl = this.sanitizer.bypassSecurityTrustResourceUrl(
        "http://dhsahospital.com/fcm/test.php?id="+this.idemail
        +"&&title="+
        this.title+" &&msg="+this.det + "&&email="+this.idemail
    );
 

    this.display=false;
    this.msgs.push({severity:'success', detail:'تم إرسال التنبيه بنجاح'});
    setTimeout(() => {
        
        this.msgs=[];
        this.loadData();
    }, 3000);
   
    this.loadData;
    
}
     notes:any[];

     
  cols: any[];
  

  title2:string='';
  det2:string='';

  display: boolean = false;

  public showDialog() {

    if(this.idemail !="0"){
        this.display = true;
    }
     else{
        this.display = false;
        this.msgs.push({severity:'error', detail:'اختر مستخدم أولا قبل إرسال تنبيه'});
        setTimeout(() => {
            this.msgs=[];
            this.router.navigate(['/users']);
 
        }, 1500);
     }
  }

  display2: boolean = false;

  id2:number=0;

  showDialog2(id,title,des) {
      this.title2=title;
      this.det2=des;
      this.id2=id;
      this.display2 = true;

    
  }

  update(){
   
    this.myurl = this.sanitizer.bypassSecurityTrustResourceUrl(
        "http://dhsahospital.com/notes/update.php?id="+this.id2 +
    " &&title="+
    this.title2+" &&des="+this.det2
    );

    this.msgs.push({severity:'success', detail:'تم تعديل التنبيه بنجاح'});
    setTimeout(() => {
        this.msgs=[];
        this.loadData();
    }, 1000);


   
  
    this.display2 = false;
    
  }

    ngOnInit() {
   
        this.route.params.subscribe(params => {
      
          //  alert(params['id']);
     
          this.idemail = params['id'];


         });
    

       this.loadData();
    
    


    }
   
loadData(){
    this.title='';
    this.det='';

    this.myserv.getNotify().subscribe(
        data=>{
            this.notes=data['notes'];
           console.log(data['notes']);

           this.countnum=data['notes'].length;
           this.lastPage=parseInt((this.countnum/4).toString())+1;
           
        }
        
    )



this.cols = [
  
        { field: 'id', header: 'الكود' },
        { field: 'name', header: 'الاسم' },
        { field: 'email', header: 'الإيميل' },
        { field: 'service_id', header: 'الحجز' },
        { field: 'payed', header: 'مؤكد' },

    ];
}

    deldata(id){
       // alert(id);
    }
    
}
