import {AppComponent} from './app.component';
import {Component} from '@angular/core';
import {trigger, state, transition, style, animate} from '@angular/animations';
import { Router } from '../../node_modules/@angular/router';



@Component({
    selector: 'app-inline-profile',
    template: `
        <div class="profile" [ngClass]="{'profile-expanded':active}">
            <a href="#" (click)="onClick($event)">
                <img style="width: 80px;
                height: 80px;
                border-radius: 50%;" class="profile-image" src="assets/imgs/doc.png" />
                <span style="font-weight:bold;" class="profile-name">إدارة العملاء والحجوزات</span>
                <i class="material-icons">keyboard_arrow_down</i>
            </a>
        </div>

        <ul class="ultima-menu profile-menu" [@menu]="active ? 'visible' : 'hidden'">
            <li role="menuitem">
                <a id="myprof" class="ripplelink" [attr.tabindex]="!active ? '-1' : null">
                    <i class="material-icons">person</i>
                    <span>الملف الشخصي</span>
                </a>
            </li>
           
            <li id="logout" (click)="logout()" role="menuitem">
                <a  class="ripplelink mylink" [attr.tabindex]="!active ? '-1' : null">
                    <i class="material-icons">power_settings_new</i>
                    <span>خروج</span>
                </a>
            </li>
        </ul>

        <style>
        #logout:hover,#myprof:hover{
            cursor:pointer;
            color:blue;
        } 
        </style>
     
    `,
    animations: [
        trigger('menu', [
            state('hidden', style({
                height: '0px'
            })),
            state('visible', style({
                height: '*'
            })),
            transition('visible => hidden', animate('400ms cubic-bezier(0.86, 0, 0.07, 1)')),
            transition('hidden => visible', animate('400ms cubic-bezier(0.86, 0, 0.07, 1)'))
        ])
    ]
})
export class AppInlineProfileComponent  {

    active: boolean;

    constructor(public app: AppComponent,public route:Router) {

    
            
      
    }

    
    onClick(event) {
        this.active = !this.active;
        setTimeout(() => {
          this.app.layoutMenuScrollerViewChild.moveBar();
        }, 450);
        event.preventDefault();
    }


    
     logout(){
        localStorage.setItem('login', 'false');
        this.route.navigate(['/login']);
        this.showcomp();     
    }

    showcomp(){
        let element = document.getElementById("rightpanel");
element.classList.remove("hidden");
element = document.getElementById("crumb");
element.classList.remove("hidden");
element = document.getElementById("footer");
element.classList.remove("hidden");
element = document.getElementById("loginfrm");
element.classList.add("hidden");
    }

    hidecomp(){
        let element = document.getElementById("rightpanel");
element.classList.add("hidden");
element = document.getElementById("crumb");
element.classList.add("hidden");
element = document.getElementById("footer");
element.classList.add("hidden");
element = document.getElementById("loginfrm");
element.classList.remove("hidden");
    }

    
}


