import { element } from 'protractor';
import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {CarService} from '../../demo/service/carservice';
import {EventService} from '../../demo/service/eventservice';
import {Car} from '../../demo/domain/car';
import {SelectItem, ConfirmationService, Message} from 'primeng/primeng';
import {BreadcrumbService} from '../../breadcrumb.service';

import {NodeService} from '../../demo/service/nodeservice';

import {TreeNode} from 'primeng/primeng';
import { Router } from '@angular/router';

import { AngularFireDatabase, AngularFireList } from 'angularfire2/database';
import { myservicesService } from '../../demo/service/myservices';
import { DomSanitizer } from '@angular/platform-browser';


import {ConfirmDialogModule} from 'primeng/confirmdialog';


@Component({
    
    templateUrl: './books.html',
    styleUrls:['./books.scss'],
    encapsulation: ViewEncapsulation.None,
    providers: [ConfirmationService],
    
})
export class BooksComponent implements OnInit {

    
    myurl:any="";

    msgs: Message[] = [];


    filter = false;

 
  


    constructor(private carService: CarService, 
        private eventService: EventService,
         private nodeService: NodeService,
        private breadcrumbService: BreadcrumbService,
        public db:AngularFireDatabase,
         public router:Router,
        private myserv:myservicesService,
        private confirmationService2:ConfirmationService,
        private sanitizer: DomSanitizer,
    ) {

            this.breadcrumbService.setItems([
                { label: 'الحجوزات' },
            ]);
     }


     appointments:any[];

     
  cols: any[];
  


  

    ngOnInit() {
   

      this.loadData();



  this.cols = [
      
            { field: 'id', header: 'الكود' },
            { field: 'name', header: 'الاسم' },
            { field: 'email', header: 'الإيميل' },
       
            { field: 'phone', header: 'الرقم' },
            { field: 'day', header: 'التاريخ' }, 
            { field: 'time', header: 'التوقيت' },
            
            { field: 'service_id', header: 'الدكتور' },
            
            { field: 'service_id2', header: 'العيادة' },
            
            { field: 'description', header: 'ملاحظات' },
            
            { field: 'payed', header: 'مؤكد' },


        ];
    
      
         
        /*
        this.appointments.forEach(element => {
                console.log('myarray='+element);
        });
         */
        



    }
   

    onFilterChange(event) {
        this.filter = !this.filter;
      //  alert(this.filter);
      if(this.filter==true){
         // console.log(this.appointments);
      }
      }

      

      okapp(val){
        this.confirmationService2.confirm({
            message: 'هل تود تأكيد الحجز رقم: « ' + val + "  » حقا ?  ",
            header: 'تأكيد الحجز رقم ' + val,
            acceptLabel:'نعم',
            rejectLabel:'لا',
            accept: () => {

                this.myurl = this.sanitizer.bypassSecurityTrustResourceUrl
                ("http://wordpress-107138-442964.cloudwaysapps.com/booking/update.php?id="+
                val);
                this.msgs.push({severity:'success', detail:'تم تأكيد الحجز بنجاح'});
              setTimeout(() => {
                  this.msgs=[];
                  this.loadData();
              }, 700);

           
    
    
    
            },
        });
   
      }
      confirm(val) {
       //  alert(val);
        this.confirmationService2.confirm({
            message: 'هل تود إلغاء الحجز رقم: « ' + val + "  » حقا ?  ",
            header: 'حذف الحجز رقم ' + val,
            acceptLabel:'نعم',
            rejectLabel:'لا',
            accept: () => {

                this.myurl = this.sanitizer.bypassSecurityTrustResourceUrl
                ("http://wordpress-107138-442964.cloudwaysapps.com/booking/del_reserve.php?id="+
                val);

                this.msgs.push({severity:'error', detail:'تم الحذف بنجاح'});
              setTimeout(() => {
                  this.msgs=[];
                  this.loadData();
              }, 700);

           
    
    
    
            },
        });
    }
  
            
    loadData(){
        this.myserv.getAppointments().subscribe(
            data=>{
                this.appointments=data['appointments'];
               console.log(data['appointments']);
            }
            
        )
    }
}
